.weather {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
  padding: 1.4rem;
  background-color: lightblue;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  text-align: center;
  color: white;
  margin-bottom: 50px; }

.weather h3 {
  display: flex;
  color: orange;
  margin: 10px;
  font-size: 1.2rem; }

.weather h2 {
  display: flex;
  font-size: 1.4rem;
  color: white; }
